.body-card {
  max-width: 200px;
  margin-right: 10px;
  transition: all 0.3s ease;
  border-radius: 2%;
  -webkit-box-shadow: 0px 0px 15px -5px rgba(171, 166, 174, 0.6) inset;
  -moz-box-shadow: 0px 0px 15px -5px rgba(171, 166, 174, 0.6) inset;
  box-shadow: 0px 0px 15px -5px rgba(171, 166, 174, 0.6) inset;
}
.body-card:hover {
  -webkit-transform: scale(1.03);
  -ms-transform: scale(1.03);
  transform: scale(1.03);
}

.circle > h3 {
  padding-left: 3%;
}
