.elipseLogo {
  display: table-cell;
  vertical-align: middle;
  margin: 0;
  width: 10%;
}
.circle {
  margin: 30px 0;
}

.btn-in-home {
  width: 300px;
  height: 40px;
  border: 1px solid#573ba3;
  background-color: inherit;
  cursor: pointer;
  /* outline: none; */
  /* border: none; */
  background-color: #fff;
  width: 140px;
  height: 40px;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 600;
  margin: 30px;
  color: #573ba3;
}

.btn-in-home:hover {
  background-color: #573ba3;
  color: #fff;
  border-color: #573ba3;
}

.block-btn-in-home {
  padding-bottom: 50px;
}

@media all and (max-width: 700px) {
  .category-title {
    font-size: 20px;
  }
  .elipseLogo {
    width: 20%;
  }
}
