.navibar__logo {
  padding-left: 20%;
}

.navibar__right {
  display: flex;
  justify-content: space-evenly;
  font-size: 13px;
  font-weight: 800;
  width: 30%;
  height: 5%;
  margin-right: 20px;
  font-size: 20px;
}
.placeholder {
  color: #fff;
  font-size: 10px;
}

.productsCount {
  display: inline-block;
  background-color: #fff;
  border-radius: 100%;
  width: 20px;
  font-size: 13px;
  color: #846bc9;
}
.shoppingBasket {
  width: 15%;
}

.btn-on-min {
  display: none;
}

@media all and (max-width: 540px) {
  .navibar__logo {
    width: 60%;
  }
  .navibar__right {
    width: 40%;
  }
  .shoppingBasket,
  .user {
    width: 30% !important;
  }

  .instagram {
    width: 17px;
    margin-right: 17px;
  }
  .productsCount {
    width: 15px;
    height: 15px;
    font-size: 10px;
  }
}
@media all and (max-width: 320px) {
  .navibar__right {
    width: 40%;
  }
  .navibar__logo {
    width: 60%;
  }
  .instagram {
    width: 17px;
    margin-right: 17px;
  }
}

@media all and (max-width: 942px) {
  .btn-on-min {
    display: flex;
    justify-content: flex-end;
    background-color: #573ba3;
  }
  .navibar__all-products {
    display: none;
  }
}

@media all and (max-width: 773px) {
  .btn-all-books {
    margin-right: 20px;
    height: auto;
    width: auto;
    font-size: 12px;
    padding: 5px;
  }
}
